.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.nav__list {
  text-transform: uppercase;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__line {
  align-items: center;
  height: 60px;
  margin-right: 20px;
  display: flex;
  position: relative;
}

.nav__line:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: 0;
  transform: scale(0);
}

.nav__line:hover:after {
  transform: scale(1);
}

.nav__link {
  color: #060b35;
  justify-content: center;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.header__logo {
  width: 40px;
  height: 40px;
}

:root {
  --main-columns-number: 1;
}

.main {
  grid-template-columns: repeat(var(--main-columns-number), 1fr);
  grid-template-rows: repeat(2, 1fr);
  justify-content: center;
  gap: 48px 46px;
  margin: 0;
  padding: 50px 40px;
  font-family: Roboto, sans-serif;
  display: grid;
}

@media (width >= 488px) {
  :root {
    --main-columns-number: 2;
  }
}

@media (width >= 768px) {
  :root {
    --main-columns-number: 3;
  }
}

@media (width >= 1024px) {
  :root {
    --main-columns-number: 4;
  }
}

.card {
  box-sizing: border-box;
  width: 200px;
  padding-inline: 16px;
  transition: transform .3s;
}

.card__image {
  width: 160px;
  margin-bottom: 40px;
  padding-top: 32px;
}

.card__title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title:hover {
  color: #34568b;
}

.card__text {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__button {
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 160px;
  height: 40px;
  margin-top: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  transform: scale(1.2);
}

.rating__block {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.rating__list {
  gap: 4px;
  padding: 0;
  list-style: none;
  display: flex;
}

.rating__list__text {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.rating__stars {
  background-image: url("star.822d561e.svg");
  background-size: contain;
  width: 16px;
  height: 16px;
}

.rating__stars:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.price__block {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.price__text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price__count {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}
